import { defineStore } from 'pinia'
// import { Message } from 'element-ui'
// import _ from 'lodash'

import { index as indexSeo, update as updateSeo } from '@/api/pages/seo'

import { useStoreConfig } from '../configStore'
import { useStorePage } from './pageStore'

const configStore = useStoreConfig()
const pageStore = useStorePage()

export const useStoreSeo = defineStore('seo', {
  state: () => ({
    configStore,
    pageStore,
    keyName: '',
    list: {},
    form: {}
  }),
  actions: {
    async indexSeo (keyName) {
      pageStore.loading = true
      return await indexSeo(keyName)
        .then((response) => {
          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    },
    async updateSeo (seoData, keyName) {
      pageStore.loading = true
      return await updateSeo(seoData, keyName)
        .then((response) => {
          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    }
  }
})
