<template>
  <el-row :gutter="10" type="flex" justify="center">
    <el-col :offset="1" :xs="22" :sm="22" :md="16" :lg="16" :xl="16">
      <el-row type="flex" class="text-color-gray">
        <el-col :span="12">
          <label style="padding-left: 10px">Editar:</label>&nbsp;&nbsp;
          <el-switch v-model="isUpdateForm"></el-switch>
        </el-col>
      </el-row>
      <div style="margin: 20px"></div>
      <el-card class="box-card" v-if="!isUpdateForm">
        <div class="text item">
          <label class="text-color-gray"
            >Título:
            <span v-if="seoStore.list.title">{{
              seoStore.list.title
            }}</span> </label
          ><br />
          <label class="text-color-gray"
            >Descripción:
            <span v-if="seoStore.list.description">{{
              seoStore.list.description
            }}</span></label
          ><br />
          <label class="text-color-gray"
            >MetaTags:
            <span v-if="seoStore.list.description">{{
              seoStore.list.metaTag
            }}</span></label
          ><br />
          <label class="text-color-gray"
            >Schema:
            <span v-if="seoStore.list.schema">{{
              seoStore.list.schema
            }}</span></label
          ><br />
          <label class="text-color-gray">Imagen:</label><br />
          <img
            v-if="seoStore.list.image"
            :src="configStore.baseUrl + seoStore.list.image"
            class="image-seo"
          />
          <div v-else>
            <span>Sin imagen</span>
          </div>
        </div>
      </el-card>
      <div style="margin-top: 20px" v-if="isUpdateForm">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <strong><span>Agregar SEO</span></strong>
          </div>
          <div class="text item">
            <ValidationObserver ref="form" v-slot="{ invalid }">
              <el-form
                label-position="top"
                label-width="120px"
                :model="seoStore.list"
              >
                <el-form-item label="Título">
                  <ValidationProvider
                    name="título"
                    rules="required|min:4|max:60"
                    v-slot="{ errors }"
                  >
                    <el-input
                      size="mini"
                      show-word-limit
                      minlength="4"
                      maxlength="60"
                      v-model="seoStore.list.title"
                    ></el-input>
                    <span>{{ errors[0] }}</span>
                  </ValidationProvider>
                </el-form-item>
                <el-form-item label="Descripción">
                  <ValidationProvider
                    name="descripción"
                    rules="required|min:10|max:160"
                    v-slot="{ errors }"
                  >
                    <el-input
                      size="mini"
                      type="textarea"
                      :rows="2"
                      minlength="10"
                      maxlength="160"
                      show-word-limit
                      v-model="seoStore.list.description"
                    ></el-input>
                    <span>{{ errors[0] }}</span>
                  </ValidationProvider>
                </el-form-item>
                <el-form-item label="Imagen">
                  <el-upload
                    class="avatar-uploader"
                    drag
                    name="image"
                    action="#"
                    :show-file-list="false"
                    :http-request="uploadImage"
                  >
                    <img
                      v-if="seoStore.list.image"
                      :src="configStore.baseUrl + seoStore.list.image"
                      alt=""
                    />
                    <template v-else>
                      <i class="el-icon-upload"></i>
                      <div class="el-upload__text">
                        Suelta tu foto aquí o
                        <em>haz clic para cargar</em>
                      </div>
                    </template>
                    <div slot="tip" class="el-upload__tip">
                      Solo archivos jpg con una medida de 800X800 pixeles
                    </div>
                  </el-upload>
                </el-form-item>
                <el-form-item label="Tags">
                  <ValidationProvider
                    name="Tags"
                    rules="required"
                    v-slot="{ errors }"
                  >
                    <el-select
                      size="mini"
                      v-model="seoStore.list.metaTag"
                      multiple
                      filterable
                      allow-create
                      placeholder="Tags"
                    >
                      <el-option
                        v-for="itemT in siteStore.optionsTags"
                        :key="itemT.value"
                        :label="itemT.label"
                        :value="itemT.value"
                      >
                      </el-option>
                    </el-select>
                    <span>{{ errors[0] }}</span>
                  </ValidationProvider>
                </el-form-item>
                <el-form-item label="Schema">
                  <ValidationProvider
                    name="schema"
                    rules="required"
                    v-slot="{ errors }"
                  >
                    <el-input
                      size="mini"
                      show-word-limit
                      v-model="seoStore.list.schema"
                    ></el-input>
                    <span>{{ errors[0] }}</span>
                  </ValidationProvider>
                </el-form-item>
                <el-form-item>
                  <el-button
                    @click.stop.prevent="checkSave"
                    type="primary"
                    size="mini"
                    icon="el-icon-upload2"
                    :disabled="invalid"
                  >
                    {{
                      siteStore.isSeoUpdate ? 'Actualizar' : 'Guardar'
                    }}</el-button
                  >
                </el-form-item>
              </el-form>
            </ValidationObserver>
          </div>
        </el-card>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import { useStoreSite } from '../../../stores/siteStores'
import { useStoreSeo } from '../../../stores/page/seoStore'
import { useStorePage } from '../../../stores/page/pageStore'
import { store as storeImageFile } from '@/api/images'
import { useStoreConfig } from '../../../stores/configStore'
import _ from 'lodash'

const siteStore = useStoreSite()
const configStore = useStoreConfig()
const seoStore = useStoreSeo()
const pageStore = useStorePage()

export default {
  name: 'Seo',
  data () {
    return {
      siteStore,
      configStore,
      seoStore,
      pageStore,
      isUpdateForm: false
    }
  },
  async created () {
    pageStore.keyName = this.$route.params.keyName
    const keyName = _.cloneDeep(pageStore.keyName)
    await seoStore.indexSeo(keyName)
  },
  methods: {
    async uploadImage (param) {
      pageStore.loading = true
      const formData = new FormData()
      formData.append('image', param.file)
      formData.append('width', '250')
      formData.append('height', '250')
      formData.append('title', 'Image')
      formData.append('alt', 'ciberseguridadSeo')
      storeImageFile(formData)
        .then((response) => {
          seoStore.list.image = response.data.path
          param.onSuccess(response.data.path)
        })
        .catch((response) => {
          // console.log(response)
          param.onError()
        })
        .finally(() => (pageStore.loading = false))
    },
    checkSave () {
      pageStore.loading = true
      const seoData = _.cloneDeep(seoStore.list)
      const keyName = _.cloneDeep(pageStore.keyName)
      seoStore
        .updateSeo(seoData, keyName)
        .then(() => {
          this.isUpdateForm = false
        })
        .finally(() => (pageStore.loading = false))
    }
  }
}
</script>
<style scoped>
.text-color-gray {
  color: #606266;
}
.image-seo {
  width: 100px;
  height: 100px;
}
.btn-js {
  background-color: #fff458;
  color: rgb(5, 5, 5);
  border-color: #fff458;
}
.btn-js:hover {
  background-color: #f0e985;
  color: rgb(5, 5, 5);
  border-color: #fff458;
}
</style>
