import request from '@/utils/request'

export function index (keyName) {
  return request({
    url: 'pages/' + keyName + '/seo',
    method: 'get'
  })
}

export function update (data, keyName) {
  return request({
    url: 'pages/' + keyName + '/seo',
    method: 'put',
    data
  })
}
